.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_image{
    width: 100%;
    height: 300px;
    z-index: -1;
    margin-bottom: -50px;
    mask-image: linear-gradient(to bottom, rgba(0,0,1),rgba(0,0,0));
}
.home_row{
    display: flex;
    z-index: 1;
    margin-left: 10px;
    margin-right: 10px;
   margin-top: 20px;
}